import styled from './Header.module.css'

const Header = ({ goHome, myMovies, submit, value, change }) => {
    return (
        <header>
            <div className='menu'>
                <div className={styled.logo} onClick={goHome}>Movies</div>
                <div className={styled.mine} onClick={myMovies}>My Movies</div>
            </div>
            <form onSubmit={submit} className={styled.search}>
                <input name='searchMovie' value={value} onChange={change} placeholder="Search movie"></input>
                <button type="submit">Search</button>
            </form>
        </header>
    )
}

export default Header