
import { useEffect, useState } from "react";
import "./index.css";
import MovieImage from "./data/MovieImage";
import Modal from "./components/Modal";
import SearchMovie from "./data/SearchMovie";
import HomeInfo from "./data/HomeInfo";
import Header from "./components/Header";

const App = () => {

  const [moviesOnScreen, setMoviesOnScreen] = useState([])
  const [movie, setMovie] = useState([])
  const [myMovies, setMyMovies] = useState([])
  const [showModal, setShowModal] = useState(false)
  const [searchMovie, setSearchMovie] = useState('')
  const [home, setHome] = useState(false)
  const [onMyMoviesPage, setOnMyMoviesPage] = useState(false)


  useEffect(() => {
    HomeInfo.getMovies().then(data => setMoviesOnScreen(data))
  }, [home])

  const handleSearchMovie = async (e) => {
    e.preventDefault()
    setOnMyMoviesPage(false)
    setSearchMovie('')
    SearchMovie.getMovies(searchMovie).then(data => setMoviesOnScreen(data));
  }

  const movieCards = moviesOnScreen.map(movie => {
    const isInMyMovies = myMovies.filter(myMovie => myMovie.id === movie.id);
    const favClass = isInMyMovies.length > 0 ? 'favorite green' : 'favorite';
    return (<div className='movie'
      key={movie.id}>
      <MovieImage  {...movie} />
      <div className="menu-movie">
        <button className="large" onClick={() => {
          setShowModal(true)
          setMovie(movie);
        }}>
        </button>
        <div onClick={(e) => {
          setMovie(movie)
          e.currentTarget.classList.toggle('green');
          console.log('hehe');

          const favMovie = myMovies.filter(myMovie => myMovie.id === movie.id)
          if (favMovie.length > 0) {
            const updateMyMovies = myMovies.filter(myMovie => myMovie.id !== movie.id)
            setMyMovies(updateMyMovies)
            if (onMyMoviesPage) setMoviesOnScreen(updateMyMovies)
          } else {
            setMyMovies([...myMovies, movie])
            console.log(myMovies);
          }
        }} className={favClass}>&#9733;</div>
      </div>

    </div>)
  })

  return (
    <div>
      <Header

        goHome={() => { setHome(!home); setShowModal(false); setOnMyMoviesPage(false) }}
        myMovies={(e) => { setMoviesOnScreen(myMovies); setShowModal(false); setOnMyMoviesPage(true) }}
        submit={handleSearchMovie}
        value={searchMovie}
        change={(e) => setSearchMovie(e.target.value)} />
      <div className="container"
        onClick={(e) => { e.stopPropagation(); if (showModal) setShowModal(false) }}>
        {showModal && <Modal
          closeModal={() => setShowModal(false)}
          {...movie} />}
        {moviesOnScreen.length === 0 && <h1>Sorry !! can't find any movie</h1>}
        <div className="grid">
          {movieCards}
        </div>

      </div>
    </div>
  );
};

export default App;
